$main-color: #1eacc7;
$second-color: #ff5656;

  @mixin btn_hover {
      background-image: none;
      background-color: $main-color;
      }


@font-face {
  font-family: Lucida Console, sans-serif;
  src: local("fonts/lucon.ttf");
}

header {
  position: relative;
  width: 1949px;
  height: 50px;
  background: #212121;
}

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2;
}

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0px;
  list-style-type: none;

.menu_link {
  font-weight: 300;
  font-size: 15px;
  color: #FFFFFF;
  }

.menu_item {
  border-right: 2px dotted black;
  padding-right: 28px;
  }
}

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background: rgba(0, 0, 0, 0.6);

.subheader_logo {
  display: block;
  max-width: 100%;
  margin-top: 16px;
}

.subheader_official {
  margin-top: 6px;
  opacity: 0.5;
  font-weight: 400;
  font-size: 12px;
  color: #FFFFFF;
}

.subheader_call {
  margin-top: 19px;
  font-weight: 300;
  font-size: 12px;
  text-align: right;
  color: #FFFFFF;
}

.subheader_phone {
  display: block;
  margin-top: 8px;
  font-weight: 400;
  font-size: 22px;
  text-align: right;
  text-transform: uppercase;
  color: #FFFFFF;
  line-height: 12px;
}

.subheader_btn {
  margin-top: 19px;
  width: 172px;
  height: 46px;
  background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  @mixin btn_hover {
    background-image: none;
    background-color: $main-color;
    
  }; 
}
}

.promo {
  min-height: 800px;
  background: url('../img/uber_bg.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-weight: 700;
    font-size: 60px;
    line-height: 60px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    margin: 0;
  }

  .promo_subheader {
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
  }

  .promo_descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    margin-top: 23px;
    text-align: center;
    color: #FFFFFF;
  }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border: none;
    background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
    border-radius: 4px;
    margin-top: 80px;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #FFFFFF;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @mixin btn_hover {

      background-image: none;
      background-color: $main-color;
    }
  }
}

.reasons {
  position: relative;
  width: 100%;
  margin: 17px;
  padding: 71px 0 65px 0;
}

.label {
  display: block;
  margin: 0 auto;
  margin-top: -30px;
  width: 115px;
  height: 27px;
  background: #000000;
  width: 113px;
  height: 24px;
  font-weight: 300;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF; 

}

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

.subtitle {
  margin-top: 0;

  font-weight: 400;
  font-size: 17px;
  color: #222222;
  text-align: center;
}

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%;
  }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      font-weight: 400;
      font-size: 17px;
      line-height: 21px;
      color: #1EACC7;
    }

    .reasons_text {
      font-weight: 300;
      font-size: 15px;
      line-height: 21px;
      color: #202020;
    }
  }
}

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
   .row {
      margin-top: 32px;
   }

  .mobile_item {
      position: relative;
      width: 320px;
      height: 280px;
      margin: 0;
      margin-bottom: 19px;
      padding-top: 92px;
}
    .mobile_item_1 {
        background: url('../img/card4.jpg') center (center / cover) no-repeat;
}
        .mobile_item_2 {
            background: url('../img/card5.jpg') center (center / cover) no-repeat;
}
            .mobile_item_3 {
                background: url('../img/card6.jpg') center (center / cover) no-repeat;
}
                .mobile_item_4 {
                     background: url('../img/card1.jpg') center (center / cover) no-repeat;

}
                    .mobile_item_5 {
                         background: url('../img/card3.jpg') center (center / cover) no-repeat;

}
                        .mobile_item_6 {
                             background: url('../img/card2.jpg');
}
.mobile_item_subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF;
}

.mobile_item_plus {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 35px;
    left: 50%;
    transform: translateX(-50%);
    width: 29px;
    height: 29px;
    background: #000000;
    border-radius: 100%;
    margin: 0 auto;
    cursor: pointer;

  }

}

.choice {
  position: relative;
  min-height: 898px;
  padding: 57px 0 138px 0;
  background: url('../img/Group\ 2.png') center (center / cover) no-repeat;
  
  .img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto;
   
  }
  
  .descr {
		display: flex;
    align-items: center;
    text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
  
  }

  .label_white {
    width: 115px;
    height: 27px;
    background: #FFFFFF;
    font-weight: 300;
    font-size: 11px;
    line-height: 11px;
    text-align: center;
    text-transform: uppercase;
    color: #070716;
  }

  .title_white {
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    display: block;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
  }

  .subtitle_white {
    font-weight: normal;
    font-size: 17px;
    line-height: 21px;
    display: block;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
  }

    .choice_img {
      display: block;
      margin: 54px auto 0 auto;
}
  .choice_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
    color: #222222;
    margin-top: 0px;
  }
}

 







 
  
 
